@import "../../helpers/styles/variables.scss";

 .button-cart {

   width: 100%;
   height: 100%;

   font-family: $font-semibold;
   font-size: 14px;
   color: #fff;
   background-color: $grey-primary-color;
   border: 1px solid $grey-primary-color;

   transition: all 0.3s;

   &:not(.button-cart--added):hover {
     border-color: $grey-elements;
     box-shadow: 0 3px 13px rgba(23, 32, 49, 0.26);
   }

   &--added {
     color: $green;
     background-color: #fff;
     border: 1px solid $grey-elements;

     &:hover {
       border: 1px solid $grey-primary-color;
     }
   }
 }
