@import "../../helpers/styles/variables.scss";

 .footer {
   display: flex;
   justify-content: center;
   align-items: center;

   height: 96px;
   width: 100%;

   background: #fff;
   box-shadow: 0 -1px 0 $grey-elements;

   font-family: $font-regular;
   font-size: 12px;
   color: $grey-secondary-color;

   &__content {
     display: flex;
     justify-content: space-between;
     width: 1136px;
   }

   &__nav {
     display: flex;
   }

   &__nav-link {
     margin-right: 64px;

     font-family: $font-bold;
     letter-spacing: 0.04em;
     text-transform: uppercase;

     transition: color 0.3s;

     &:hover {
       color: $grey-primary-color;
     }

     &:last-child {
       margin-right: 0;
     }
   }

   &__button-img {
     width: 32px;
     height: 32px;
     margin-left: 16px;

     border: 1px solid $grey-icons;
     transition: border-color 0.3s;
   }

   &__button {
     display: flex;
     align-items: center;

     line-height: 15px;
     font-family: $font-semibold;
     color: $grey-secondary-color;

     transition: color 0.3s;

     &:hover {
       color: $grey-primary-color;

       & .footer__button-img {
         border-color: #313237;
       }
     }
   }
 }
