@import "../../helpers/styles/variables.scss";

 .pagination {
   display: flex;
   justify-content: center;
   align-items: center;

   &__button {
     display: flex;
     justify-content: center;
     align-items: center;
     width: 32px;
     height: 32px;
     background-color: #fff;
     border: 1px solid $grey-icons;

     transition: all 0.3s;
     background-repeat: no-repeat;
     background-position: center;

   }

   &__button-left {
     background-image: url(../../images/icons/button_banner_left.svg);

     &:not(.pagination__button-left--disabled):hover {
       border-color: $grey-primary-color;
     }

     &--disabled {
       border-color: $grey-elements;
       cursor: none;
       pointer-events: none;

       background-image: url(../../images/icons/button_arrow_left.svg);
     }
   }

   &__button-right {
     background-image: url(../../images/icons/button_banner_right.svg);

     &:not(.pagination__button-right--disabled):hover {
       border-color: $grey-primary-color;
     }

     &--disabled {
       border-color: $grey-elements;
       cursor: none;
       pointer-events: none;

       background-image: url(../../images/icons/button_arrow_right.svg);
     }
   }

   &__list {
     display: flex;
     margin: 0 16px;
   }

   &__item {
     margin-right: 8px;

     &--active {
       color: #fff;
       background-color: $grey-primary-color;
     }

     &:last-child {
       margin-right: 0;
     }
   }

   &__link {
     display: flex;
     justify-content: center;
     align-items: center;

     width: 32px;
     height: 32px;

     border: 1px solid $grey-icons;
     transition: all 0.3s;

     &:hover {
       border-color: $grey-primary-color;
     }

     &--active {
       color: #fff;
       background-color: $grey-primary-color;
     }
   }

   &__break {
     display: flex;
     justify-content: center;
     align-items: center;

     width: 16px;
     height: 32px;
   }
 }
