@import "../../helpers/styles/variables.scss";

 .product-list {

   &__items {
     width: 1136px;
     display: flex;
     flex-wrap: wrap;
     margin-bottom: 40px;
   }

   &__item {
     display: block;
     margin-right: 16px;
     margin-bottom: 16px;

     &:nth-child(4n) {
       margin-right: 0;
     }
   }

   &__drop-downs {
     display: flex;
     justify-content: flex-start;
     margin-bottom: 24px;
   }

   &__drop-down {
     &:first-child {
       margin-right: 16px;
     }
   }

   &__pagination {
     display: flex;
     justify-content: center;
   }
 }
