@import "../../helpers/styles/variables.scss";

 .product-info {
   display: flex;
   width: 100%;

   &__title {
     padding-bottom: 16px;

     border-bottom: 1px solid $grey-elements;
     font-family: $font-bold;
     font-size: 22px;
   }

   &__title-about {
     margin-bottom: 32px;
   }

   &__title-tech {
     margin-bottom: 24px;
   }

   &__about {
     width: 560px;
     margin-right: 64px;
   }

   &__description-title {
     margin: 0 0 16px;
     font-family: $font-semibold;
     font-size: 20px;
   }

   &__description-paragraph {
     margin-bottom: 32px;
     color: $grey-secondary-color;
     font-size: 14px;
   }

   &__tech-specs {
     width: 512px;
   }

   &__tech-item {
     display: flex;
     justify-content: space-between;
   }

   &__tech-item-title {
     margin-bottom: 8px;

     color: $grey-secondary-color;
     font-size: 14px;
     line-height: 21px;
   }

   &__tech-item-value {
     font-size: 14px;
     line-height: 21px;
   }
 }
