@import "../../helpers/styles/variables.scss";

 .cart-info {

   &__content {
     display: flex;
     flex-direction: column;
     align-items: center;
     margin-bottom: 24px;
     width: 368px;
     height: 206px;
     padding: 24px;

     border: 1px solid $grey-elements;
   }

   &__box {
     width: 100%;
     display: flex;
     flex-direction: column;
     align-items: center;
     margin-bottom: 24px;
     padding-bottom: 24px;
     border-bottom: 1px solid $grey-elements;
   }

   &__total-price {
     margin: 0;
   }

   &__total-items {
     font-size: 14px;
     color: $grey-secondary-color;
   }

   &__button {
     width: 320px;
     height: 48px;

     font-size: 14px;
     font-family: $font-semibold;
     color: #fff;
     background-color: $grey-primary-color;

     transition: all 0.3s;

     &:hover {
       box-shadow: 0 3px 13px rgba(23, 32, 49, 0.26);
     }
   }

   &__checkout-message {
     display: flex;
     justify-content: center;
     align-items: center;
     width: 368px;
     height: 100px;
     padding: 24px;

     font-size: 20px;
     font-family: $font-bold;

     border: 4px solid $grey-primary-color;
   }
 }
